
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

import { setInterval, clearInterval, setTimeout } from 'timers'
import animations from 'create-keyframe-animation'
import _ from 'lodash'
export default {
components: {
Flexbox,
FlexboxItem,
Divider
},
data() {
return {
list: new Array(10),
listClone: [],
timer: null,
delay: 300,
requestIndex: 0,
registerDelay: 25,
baseUrl: "fileupdown/downloadBusiFile?filePath=",
current: +new Date()
}
},
computed: {
win_width() {
return window.innerWidth
}
},
mounted () {
this.timer = setInterval(() => {
this._getData()
}, this.delay)

},
methods: {
_getData() {

let url = '/iscportal/conferenceSigning/get';
AjaxPlugin.$http.post(url).then(res => {

if(res.status === 200 && res.data.returnCode === '0000') {

let newList = this._stringToJson(res.data.ext1.abc)

let isEqual = _.isEqual(newList, this.listClone)

this.requestIndex++
if(this.requestIndex === 1) {

this.list = newList

} else {

if (isEqual) {
console.log('后台数据与备份数据相同')
return
}
}


this.listClone = newList

this.list = this.list.concat(newList)

this.list = _.uniq(this.list)



this.list.sort((a, b) => b.capturedTime - a.capturedTime)
console.log('取前十个之前 -----> ')
console.log(this.list)
if(this.list.length >= 10) {
this.list = this.list.slice(0, 10)
}
console.log('取前十个之后 -----> ')
console.log(this.list)
if(this.requestIndex !== 1) {
animations.unregisterAnimation('move')
}


setTimeout(() => {
this._setAnimation()
}, this.registerDelay)









}

})
},
_setAnimation() {
let animation = {
0: {
transform: `translate(0, 0)`
},
100: {
transform: `translate(${this.win_width / 8}px, 0)`

}
}
animations.registerAnimation({
name: 'move',
animation,
presets: {
duration: this.delay,
easing: 'linear'
}
})

animations.runAnimation(this.$refs.flexBox.$el, 'move')
},
_stringToJson(arr) {
if(!arr) return
let ret = []
arr.map((item) => {
let json = JSON.parse(item)
ret.push(json)
})
return ret
},
_formatTime(time) {
if(time === 'null') {
return '';
}
let date = time? new Date(time): new Date();
let month = date.getMonth()<9? `0${date.getMonth()+1}`: (date.getMonth()+1);
let day = date.getDate()<10? `0${date.getDate()}`: date.getDate();
let hours = date.getHours()<10?`0${date.getHours()}`:date.getHours();
let min = date.getMinutes()<10?`0${date.getMinutes()}`:date.getMinutes();
let sec = date.getSeconds()<10?`0${date.getSeconds()}`:date.getSeconds();
return date.getFullYear()+'-'+month+'-'+day+ " " +hours+':'+min+':'+sec;
}
},
destroyed() {
clearInterval(this.timer)
}
}



